<template>
    <div class="nav-item d-inline">
        <div class="nav-link" v-b-modal.sign-up>
          <p class="text-white">signup</p>
        </div>

        <b-modal
        id="sign-up"
        ref="modal"
        title=""
        @show="resetModal"
        @hidden="resetModal"
        hide-footer
        >
        <form ref="form" @submit.stop.prevent="handleSubmit" accept-charset="utf-8">
            <b-form-group
                label="ID"
                label-for="uid-input"
            >
            <b-form-input
                id="uid-input"
                v-model="userData.uid"
                required
                placeholder="Enter ID"
            ></b-form-input>
            </b-form-group>

            <b-form-group
                label="NICKNAME"
                label-for="name-input"
            >
            <b-form-input
                id="name-input"
                v-model="userData.name"
                required
                placeholder="Enter NICKNAME"
            ></b-form-input>
            </b-form-group>

            <b-form-group
                label="PASSWORD"
                label-for="password-input"
            >
            <b-form-input
                type="password"
                id="password-input"
                v-model="password1"
                required
                placeholder="Enter Password"
            ></b-form-input>
            </b-form-group>

            <b-form-group
                label="CONFIRM PASSWORD"
                label-for="password-input"
            >
            <b-form-input
                type="password"
                id="password-input"
                v-model="password2"
                required
                placeholder="Check Password"
            ></b-form-input>
            </b-form-group>

            <b-form-group
              id="input-group-1"
              label="Email"
              label-for="email-input"
              description="  ."
            >
              <b-form-input
                id="email-input"
                v-model="userData.email"
                type="email"
                required
                :state="emailAuthChk"
                placeholder="Enter email"
              ></b-form-input>
              <b-button @click="emailAuth" v-if="emailCode===''">  </b-button>
            </b-form-group>

            <b-form-group
              label="Email Code"
              label-for="email-code-input"
              v-if="!emailAuthChk && emailCode !==''"
            >
              <b-form-input
                id="email-code-input"
                v-model="emailCodeInput"
                type="text"
                placeholder=" 4  "
              ></b-form-input>
              <b-button @click="emailAuth">  </b-button>
              <b-button @click="codeChk">  </b-button>
            </b-form-group>

            <b-form-group id="input-group-3" label="1" label-for="inter1-input">
              <b-form-select
                id="inter1-input"
                v-model="userData.interest1"
                :options="interests"
                required
              ></b-form-select>
            </b-form-group>

            <b-form-group id="input-group-3" label="2" label-for="inter1-input" description=" 1  ">
              <b-form-select
                id="inter1-input"
                v-model="userData.interest2"
                :options="interests"
                required
              ></b-form-select>
            </b-form-group>

            <b-button class="mt-3" block @click=handleSubmit></b-button>
        </form>
        </b-modal>
    </div>

</template>

<script>
import axios from 'axios'
import { sync } from 'vuex-pathify'


export default {
    name: 'Signup',
    data() {
        return {
            API_URL: '',
            emailCode: '',
            emailCodeInput: '',
            emailAuthChk: false,

            userData: {
              email: '',
              uid: '',
              password: '',
              name: '',
              phone: '',
              interest1: '',
              interest2: '',
            },

            password1: '',
            password2: '',

            interests: [
              { text: '   ', value: '' },
              '',
              '',
              'IT',
              '',
              '',
              '',
              '',
              '',
              '',
              '',
            ],
        }
    },

    computed: {
        curUid: sync('curUid'),
        DisURL: sync('DisURL'),
        LocalURL: sync('LocalURL'),
        isLocal: sync('isLocal'),
    },

    mounted() {
        if (this.isLocal){
            this.API_URL = this.LocalURL
        } else {
            this.API_URL = this.DisURL
        }
    },

    methods: {
        resetModal() {
            this.userData.uid = ''
            this.userData.email = ''
            this.password1 = ''
            this.password2 = ''
            this.userData.password = ''
            this.userData.name = ''
            this.userData.phone = ''
            this.userData.interest1 = ''
            this.userData.interest2 = ''

            this.emailCode = ''
            this.emailCodeInput = ''
            this.emailAuthChk = false
        },
        handleSubmit() {
            // Exit when the form isn't valid
            if (this.userData.uid === '') {
                alert('ID ')
                return
            }
            if (this.userData.email === '') {
              alert('EMAIL  ')
              return
            }
            if (this.userData.name === '') {
              alert('NICKNAME  ')
              return
            }
            if (this.password1 === '' || this.password2 === '') {
                alert('PASSWORD ')
                return
            }
            if (this.password1 !== this.password2) {
              alert('PASSWORD  ')
              return
            }
            if (this.userData.interest1 === '' || this.userData.interest2 === '') {
              alert(' ')
              return
            }
            if (this.userData.interest1 === this.userData.interest2) {
              alert('   ')
              return
            }

            if (!this.emailAuthChk) {
              alert('  ')
              return
            }

            this.signup();
        },

        signup() {
          const url = `${this.API_URL}account/join`
          this.userData.password = this.password1

          console.log(this.userData)

          console.log(url)
          axios.post(url, this.userData)
          .then(res => {
            console.log(res)
            this.login()
          })
          .catch((err) => {
            console.log(err.response)
            if (err.response.data.data === 'Uid') { 
              alert('  ')
            }
            else if (err.response.data.data === 'Email') {
              alert('  ')
            }
            else {
              alert("  ")
            }
            return
          });
        },


        login() {
          const url = `${this.API_URL}account/login?uid=${this.userData.uid}&password=${this.userData.password}`;
          console.log(url);
          axios
          .get(url)
          .then(({data}) => {
              console.log(data);
              this.curUid = this.userData.uid;
              this.$nextTick(() => {
              this.$bvModal.hide('sign-up')
              })
              console.log('login_success');
              this.$router.push('/')
          })
          .catch(() => {
              alert("ID  PASSWORD ")
              return
          });
        },

        emailAuth() {
          const url = `${this.API_URL}account/email_auth?email=${this.userData.email}`
          axios
          .get(url)
          .then(res => {
            this.emailCode = res.data.data
            return
          })
          .catch(() => {
            alert('  ')
            return
          })
        },

        codeChk() {
          if (this.emailCodeInput === this.emailCode) {
            this.emailAuthChk = true
            alert(' ')
          }
          else {
            alert('   ')
          }
          return
        },
    }
}
</script>

