<template>
    <div class="d-inline">

        <b-button class="btn btn-primary btn-round btn-lg" v-b-modal.des-update-modal>  </b-button>

        <b-modal
        id="des-update-modal"
        ref="modal"
        title="Description"
        @hidden="resetModal"
        hide-footer
        >
        <form ref="form" @submit.stop.prevent="handleSubmit" accept-charset="utf-8">
            <b-form-group
                label=""
                label-for="name-input"
                description="500  "
            >
            <b-form-textarea
              id="textarea-state"
              v-model="userData.description"
              placeholder="  "
              rows="3"
            ></b-form-textarea>
            </b-form-group>

            <b-button class="mt-3" block @click=handleSubmit>  </b-button>
        </form>
        </b-modal>
    </div>

</template>

<script>
import axios from 'axios'
import { sync } from 'vuex-pathify'

export default {
    name: 'ProfileUpdate',

    data() {
      return {
        API_URL: '',
        interests: [
          { text: '   ', value: '' },
          '',
          '',
          'IT',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
        ],
        tempDes: '',
      }
    },
    
    props: ['userData'],

    computed: {
        curUid: sync('curUid'),
        DisURL: sync('DisURL'),
        LocalURL: sync('LocalURL'),
        isLocal: sync('isLocal'),
    },

    methods: {
        resetModal() {
            this.userData.description = this.tempDes
        },

        handleSubmit() {

            if (this.userData.description.length > 500) {
                alert('500  ')
                return
            }

            var putData = {
                uid: this.curUid,
                description: this.userData.description,
            }
            
            const url = `${this.API_URL}account/update/description`

            axios.put(url, putData)
            .then(res => {
                this.$nextTick(() => {
                this.$bvModal.hide('des-update-modal')
                })

                console.log('description_update_success')
                console.log('utf-8 ')
                console.log(putData.description)
                this.tempDes = this.userData.description
                location.reload()
                alert('  ')
            })
            .catch(() => {
                alert('   .')
                return
            })
            
        },
    },
    
    mounted() {
        this.tempDes = this.userData.description

        if (this.isLocal){
            this.API_URL = this.LocalURL
        } else {
            this.API_URL = this.DisURL
        }
    }
}
</script>

