<template>
  <div class="row">
      <div class="col-sm-6 col-lg-9">
          <fg-input placeholder=" ." v-model="commentData.content"></fg-input>
      </div> 
      <div class="col-sm-3 col-lg-3"><n-button type="primary" class="mt-0 mb-0" round @click="createComment" v-if="curUid && commentData.content !==''"></n-button></div>
  </div>
</template>


<script>
import axios from 'axios'
import { sync } from 'vuex-pathify'

import Button from '@/pages/board/comment/Button.vue'
import formGroupInput from '@/pages/board/comment/formGroupInput.vue'

export default {
  name: 'commentInput',
  data() {
    return {
      commentData: {
        uid: '',
        boardno: null,
        content: '',
      },

      API_URL: '',
    }
  },
  methods: {
    createComment() {
      this.commentData.boardno = this.$route.params.boardno
      this.commentData.uid = this.curUid
      axios.post(`${this.API_URL}comment/create/`, this.commentData)
      .then(res => {
        this.$emit('submitComment')
        this.commentData.content = ''
      })
      .catch(err => {
        console.log(err)
      })
    }
  },
  mounted() {
    if (this.isLocal){
    this.API_URL = this.LocalURL
    } else {
    this.API_URL = this.DisURL
    }
  },
  components: {
    [Button.name]: Button,
    [formGroupInput.name]: formGroupInput,
  },
  computed: {
    curUid: sync('curUid'),
    DisURL: sync('DisURL'),
    LocalURL: sync('LocalURL'),
    isLocal: sync('isLocal'),
  },
  
}
</script>

